*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;

}

h2 {
  font-size: 30px;
}
header { 
  padding: 20px 35px;
  background-color:#db7093;

}
header h1{
  font-weight: bold;
  font-size: 36px;
  color: #7b68ee;
  margin: 0;
  display: inline;
}
header a{
  text-decoration: none;
  color:#7b68ee;
}
header nav {
  margin: 7px 0;
  float: right;
}
header nav ul li{
  display: inline;
  float:right;
  margin: 7px 0;
}
/* margin-left: auto - pushes all margin to the left
OR Make parent element display: lfex and set content to justify-content: space-between;
Play with display: flex and justify-content (flex-start, flex-end, space-around, space-between)
*/

header nav ul li a{
  margin: 0 30px;
  font-weight: lighter;
  font-size: 22px;
}
body {
  color:#db7093;
  font-family:'Times New Roman', Times, serif;
  background-color:#f4f0ec;
}
.hero {
  background-image: url(./images/Blue_Ridge_Mountains.png);
  height: 600px;
  background-size: cover;
  background-position: right;
  position: relative;
}
.hero-form p{
  border:3px solid #add8e6;
  background-color:#add8e6;
  padding: 20px;
  width: 500px;
  color:#7f00ff;
  position:absolute;
  bottom: 120px;
  right: 140px;
  border: 3px solid gray;

}
.intro{
  text-align: left;
  padding:50px;
  font-size:25px;
  margin: auto;
  display: flex;
  flex-direction:row;
  flex-wrap: wrap;
}
.intro p{ line-height: 1.7;
  color: #db7093;
  width: 80%;
  font-size: 25px;
  margin: 7px;
}
.intro h2, .work h2, .contact-me h2 {
  border-right:5px solid gray;
  margin-right:20px;
  padding-right:20px;
  width: 175px;

}
.work{
  display: flex;
  flex-direction:row;
  /*flex-wrap: wrap;*/
  text-align:left;
  padding:60px;
  /* margin:auto; */

}
.work a{
  text-decoration: none;
}
.project{
  background: #add8e6;
  color:black;
  font-size: 25px;
  text-align: left;
  border: 3px solid gray;
}


.project:hover{
  background-color:aliceblue;
}
.project img{
  width: 100%;
  opacity:50%;
}
.project img:hover{
  opacity: 100%;
}
.smaller-boxes{
  display:flex;
  flex-direction:row;
  flex-wrap:wrap;
  justify-content: flex-start;
}

/* smaller boxes specifically */
.smaller-boxes article {
  width: 35%;
  margin: 30px 5% 30px 0;

}
.contact-me{
  padding:50px;
  margin:auto;
}
@media screen and (max-width:600px) {
  body{
      
  }
  .work{
      flex-direction:column;
  }
  
}
.run-buddy{
  width: 75%;

}
.contact-me{
  padding: 20px 50px;
  text-decoration:#db7093;
  margin: 0 auto;
  display:flex;
  
}
.contact-me nav ul li{
  display: inline;
  float: right;
  margin: 30px 5% 30px 0;
  padding-bottom: 0px;
  color: #db7093;

}
.contact-me nav ul{
  display:flex;
  justify-content: space-evenly;
}
.contact-me div{
  width:70%;

}
.contact-me a{
  color:#db7093;
}
.audrey{
  width:65%;
  display:flex;

}
.container{
      width: auto;
      height: 300px;
      overflow: hidden;
    }
    
.container img {
      max-width: 100%;
      height: auto;
      display: block;
    }
